import db from "./firebase";
import { useEffect, useState } from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

const DataFetch = () => {
  //default app states
  const [allResumes, setAllResumes] = useState([]);

  // initialize component with function start
  useEffect(() => {
    getDataToDash();
  }, []);

  // allows get into firestoredatabase
  const getDataToDash = async () => {
    const response = db.collection("bancotalentosFinal");
    const data = await response.get();
    const allCollectionItems = data.docs.map((item) => item.data());
    setAllResumes(allCollectionItems);
  };
  console.log(allResumes);
  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell>Empresario</TableCell>
              <TableCell>Empresa Jr</TableCell>
              <TableCell>O que vai aqui</TableCell>
              <TableCell>e aqui?</TableCell>
              <TableCell>KKK thorzinho god</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allResumes.map((row) => (
              <TableRow
                key={row.name}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
                <TableCell component="th" scope="row">
                  {row.colectedData.nome}
                </TableCell>
                <TableCell align="right">{}</TableCell>
                <TableCell align="right">{row.fat}</TableCell>
                <TableCell align="right">{row.carbs}</TableCell>
                <TableCell align="right">{row.protein}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataFetch;
